<template>
	<div class="result calc-section">
		<div v-if="result" class="result-container">
			Период потенциальной опасности РАО:
			<div class="calc-result">
				{{ result }}
			</div>
		</div>
		<div
			class="active-item"
			v-for="(item, i) in active"
			:key="'active_item_' + item.ID"
		>
			<div class="stats">
				<span class="active-stat"
					>Наименование:
					<span class="name">{{ item.item.Name_RN }}</span>
				</span>
				<span class="active-stat">{{
					'Обозначение: ' + item.item.Name_RN_Lat
				}}</span>
				<span class="active-stat">{{
					'Период полураспада: ' +
					item.item.Period_p_r +
					' ' +
					item.item.Edinica_izmer_p_r
				}}</span>
			</div>

			<div class="active-input-container" v-if="rnType == 'Полинуклидный'">
				<span>Доля активности, %</span>
				<input
					:class="['active-input', { error: !percentOk }]"
					type="number"
					:value="Number(item.percent.toFixed(2))"
					@input="percentInputHandler($event, i)"
				/>
			</div>

			<div class="active-input-container">
				<span>Масса</span>
				<div class="mass-input-container">
					<input
						:class="['active-input', { error: item.rawMass <= 0 }]"
						type="number"
						:value="Number(item.rawMass)"
						@input="massInputHandler($event, i)"
					/>
					<select
						class="mass-unit-select"
						v-model="item['massUnit']"
						@change="massUnitChangeHandler($event, i)"
					>
						<option>г</option>
						<option>кг</option>
						<option>т</option>
					</select>
				</div>
			</div>

			<button class="remove-btn" @click="removeClickHandler(i)">X</button>
		</div>
	</div>
</template>

<script>
export default {
	name: 'Result',
	props: {
		active: Array,
		percentOk: Boolean,
		result: String,
		rnType: String
	},
	data() {
		return {}
	},
	methods: {
		removeClickHandler(i) {
			this.$emit('remove-item', i)
		},
		percentInputHandler(e, i) {
			const value = e.target.value
			this.$emit('percent-input', i, value)
		},
		massInputHandler(e, i) {
			const value = e.target.value
			this.$emit('mass-input', i, value)
		},
		massUnitChangeHandler(e, i) {
			const value = e.target.value
			this.$emit('mass-unit-change', i, value)
		}
	}
}
</script>

