<template>
	<div class="calc">
		<List :list="filteredList" @add-item="addItem" />
		<div class="right">
			<Setup
				:percentOk="percentOk"
				:massOk="massOk"
				:countOk="countOk"
				:rnType="rnType"
				@calculate="calculate"
				ref="setup"
				@rnTypeChange="rnTypeChangeHandler"
			/>
			<Result
				:active="active"
				:percentOk="percentOk"
				:result="result"
				:rnType="rnType"
				@remove-item="removeItem"
				@percent-input="percentInput"
				@mass-input="massInput"
				@mass-unit-change="massUnitChange"
			/>
		</div>
	</div>
</template>

<script>
import List from './list.vue'
import Setup from './setup.vue'
import Result from './result.vue'

// convert time in specified format to years
const getYears = (time, format) => {
	let mult
	switch (format.slice(0, 3)) {
		case 'мин':
			mult = 1
			break
		case 'час':
			mult = 60
			break
		case 'сут':
			mult = 1440
			break
		case 'лет':
			mult = 525948.768 // 525600
			break
		default:
			return 0
	}
	return (time * mult) / 525948.768
}

export default {
	name: 'CalcPeriodPotentialDanger',
	components: { List, Setup, Result },
	props: {
		url: String
	},
	data() {
		return {
			rnType: 'Мононуклидный',
			list: [],
			active: [],
			result: ''
		}
	},
	computed: {
		filteredList() {
			const activeIds = this.active.map(item => item.item.ID)
			return this.list.filter(item => !activeIds.includes(item.ID))
		},
		totalPercent() {
			let totalPercent = this.active.reduce((p, c) => p + c.percent, 0)
			totalPercent = Number(totalPercent.toFixed(12)) // fix 100.0000000000001
			return totalPercent
		},
		percentOk() {
			return this.active.length != 0 && this.totalPercent == 100
		},
		massOk() {
			return this.active.every(item => item.rawMass > 0)
		},
		countOk() {
			return !(this.rnType == 'Полинуклидный' && this.active.length == 1)
		}
	},
	methods: {
		addItem(item) {
			// if rn type is mono, but the second item from list is selected
			// do nothing
			if (this.rnType == 'Мононуклидный' && this.active.length > 0) {
				this.$refs.setup.shakeTypeSelector()
				return
			}

			// calculate percentages
			let percent
			if (this.totalPercent == 100) {
				// existing items sum up to 100%
				if (
					this.active.filter(i => i.percent == 100 / this.active.length)
						.length == this.active.length
				) {
					// even percentage spread
					percent = 100 / (this.active.length + 1)
					this.active.forEach(i => (i.percent = percent))
				} else {
					// uneven percentage spread
					percent = 0
				}
			} else {
				// existing items don't sum up to 100%
				percent = 100 - this.totalPercent
			}

			this.active.push({
				item,
				percent,
				rawMass: 0,
				massUnit: 'г',
				get massG() {
					let unitMap = {
						г: 1,
						кг: 1000,
						т: 1000000
					}
					return this.rawMass * unitMap[this.massUnit]
				}
			})

			this.resetCalculations()
		},
		removeItem(i) {
			const percent = this.active.splice(i, 1)[0].percent

			if (this.active.length) {
				if (this.active.length == 1) {
					// only one active item left, set it to 100%
					this.active[0].percent = 100
				} else {
					// more than one item left
					// add percentage of removed to the first active item
					this.active[0].percent += percent
				}
			}

			this.resetCalculations()
		},
		percentInput(i, value) {
			value = parseFloat(value) || 0
			if (!value) this.active[i].percent = 1 // force update of the input
			this.active[i].percent = value

			this.resetCalculations()
		},
		massInput(i, value) {
			value = parseFloat(value) || 0
			if (!value) this.active[i].rawMass = 1 // force update of the input
			this.active[i].rawMass = value

			this.resetCalculations()
		},
		massUnitChange(i, value) {
			this.active[i].massUnit = value
			this.resetCalculations()
		},
		rnTypeChangeHandler(val) {
			this.rnType = val

			if (val == 'Мононуклидный' && this.active.length > 1) {
				this.active = [this.active[0]]
				this.active[0].percent = 100
			}
		},
		calculate(aBk) {
			// calculate potential danger

			// calculation depends on rnType
			// and agrState
			const agrState = this.$refs.setup.agrState
			const rnType = this.rnType

			if (rnType == 'Мононуклидный') {
				const item = this.active[0]

				const massG = item.massG

				// udA = a / mass
				const a0 = aBk / massG
				const a =
					agrState == 'Твердое'
						? item.item.UdA_TRO
						: item.item.UdA_GRO * 0.1
				const T = getYears(
					item.item.Period_p_r,
					item.item.Edinica_izmer_p_r
				)

				// t = 1,144 * T * ln(A0/A)
				const result = 1.144 * T * Math.log(a0 / a)

				this.result = result + ' лет'

				// LOGS ------

				console.log('------')
				console.log('формула: t = 1,144 * T * ln (a0 / a)')
				console.log(`a0 = aBk / massG = ${aBk} / ${massG} = ${a0}`)
				console.log(`a = ${a}`)
				console.log(
					`T = ${item.item.Period_p_r} ${item.item.Edinica_izmer_p_r} = ${T} лет`
				)
				console.log(`t = 1,144 * ${T} * ln (${a0} / ${a})`)
				console.log(`результат = ${this.result}`)
			} else {
				// состав полинуклидный

				// returns ud activity after n years
				// this function depends on context and is not reusable
				const udActivityIn = (years, item) => {
					const a = (aBk / 100) * item.percent
					return (
						(a * 2 ** -years) /
						getYears(item.item.Period_p_r, item.item.Edinica_izmer_p_r) /
						item.massG
					)
				}

				// for each selected rn,
				// calculate its activity in 100 & 500 years
				// based on its % of summary activity
				// and divide each rn's activity in n years by its % of mass

				// udA100 = udA in 100 years
				this.active.forEach(item => {
					item.udA100 = udActivityIn(100, item)
				})

				// udA500 = udA in 500 years
				this.active.forEach(item => {
					item.udA500 = udActivityIn(500, item)
				})

				// for each rn,
				// divide its udA (in n years) by UdA (from json)
				// and then sum all the results for 100 and 500 years respectively

				const udAProp = agrState == 'Твердое' ? 'UdA_TRO' : 'UdA_GRO'

				// t100 = sum of all (udA100 / UdA_json)
				const t100 = this.active
					.map(item => item.udA100 / item.item[udAProp])
					.reduce((p, c) => p + c)

				// t500 = sum of all (udA500 / UdA_json)
				const t500 = this.active
					.map(item => item.udA500 / item.item[udAProp])
					.reduce((p, c) => p + c)

				// comparison coefficient
				const c = agrState == 'Твердое' ? 1 : 0.1

				// if t100 > c => менее 100 лет
				if (t100 > c) this.result = 'менее 100 лет'
				// if t100 < c && t500 > c => от 100 до 500 лет
				else if (t100 < c && t500 > c) this.result = 'от 100 до 500 лет'
				// if t500 < c => более 500 лет
				else if (t500 < c) this.result = 'более 500 лет'

				// LOGS ------

				console.log('------')
				console.log('удельные активности через 100 лет:')
				console.log('формула: (a0 * 2 ^ -100) / Period_p_r / mass')
				console.log(
					this.active
						.map(
							item =>
								item.item.Name_RN +
								' = (' +
								(aBk / 100) * item.percent +
								' * 2 ^ -100) / ' +
								getYears(
									item.item.Period_p_r,
									item.item.Edinica_izmer_p_r
								) +
								' / ' +
								item.massG +
								' = ' +
								item.udA100
						)
						.join('\n')
				)
				console.log('удельные активности через 500 лет:')
				console.log('формула: (a0 * 2 ^ -500) / Period_p_r / mass')
				console.log(
					this.active
						.map(
							item =>
								item.item.Name_RN +
								' = (' +
								(aBk / 100) * item.percent +
								' * 2 ^ -500) / ' +
								getYears(
									item.item.Period_p_r,
									item.item.Edinica_izmer_p_r
								) +
								' / ' +
								item.massG +
								' = ' +
								item.udA500
						)
						.join('\n')
				)
				console.log(
					'сумма отношений удельных активностей через 100 лет к значениям из справочника'
				)
				console.log('формула: E ( udA100 / udA_json )')
				console.log(
					this.active
						.map(
							item =>
								' ( ' + item.udA100 + ' / ' + item.item[udAProp] + ' ) '
						)
						.join('+') +
						' = ' +
						t100
				)
				console.log(
					'сумма отношений удельных активностей через 500 лет к значениям из справочника'
				)
				console.log('формула: E ( udA500 / udA_json )')
				console.log(
					this.active
						.map(
							item =>
								' ( ' + item.udA500 + ' / ' + item.item[udAProp] + ' ) '
						)
						.join('+') +
						' = ' +
						t500
				)
				if (t100 > c) {
					console.log(`(сумма через 100 лет) ${t100} > ${c}`)
					console.log('=> результат: менее 100 лет')
				} else if (t100 < c && t500 > c) {
					console.log(`(сумма через 100 лет) ${t100} < ${c}`)
					console.log(`(сумма через 500 лет) ${t500} > ${c}`)
					console.log('=> результат: от 100 до 500 лет')
				} else if (t500 < c) {
					console.log(`(сумма через 500 лет) ${t500} < ${c}`)
					console.log('=> результат: более 500 лет')
				}
			}
		},
		resetCalculations() {
			this.result = ''
		}
	},
	created() {
		// fetch json
		fetch(this.url)
			.then(res => res.json())
			.then(data => {
				this.list = data.filter(item => {
					// filter out all the items with no required information specified
					return (
						item.Name_RN &&
						item.Kod_RN &&
						item.Period_p_r &&
						item.Name_RN_Lat &&
						item.Edinica_izmer_p_r &&
						item.UdA_TRO &&
						item.UdA_GRO
					)
				})
			})
	}
}
</script>

<style scoped lang="scss">
.calc {
	display: flex;
	.right {
		display: flex;
		flex-direction: column;
		flex-grow: 1;
	}
	.calc-section {
		background: #ecf5fa;
		margin: 10px;
		border-radius: 3px;
	}
}
</style>
