<template>
	<div class="setup calc-section">
		<div class="setup-col-2">
			<div :class="['center', { shake: shakeType }]">
				<span class="title">Состав</span>
				<div class="agr-state-buttons-container">
					<button
						v-for="state of ['Мононуклидный', 'Полинуклидный']"
						:value="state"
						:class="['agr-state-button', { pressed: rnType == state }]"
						:key="'rnTypeStateButton_' + state"
						@click="rnTypeStateButtonClickHandler"
					>
						{{ state }}
					</button>
				</div>
			</div>
			<div class="center">
				<span class="title">Агрегатное состояние</span>
				<div class="agr-state-buttons-container">
					<button
						v-for="state of ['Твердое', 'Жидкое']"
						:value="state"
						:class="['agr-state-button', { pressed: agrState == state }]"
						:key="'agrStateButton_' + state"
						@click="agrStateButtonClickHandler"
					>
						{{ state }}
					</button>
				</div>
			</div>
		</div>

		<span class="title">
			{{ (rnType == 'Полинуклидный' ? 'Суммарная а' : 'А') + 'ктивность' }}
		</span>
		<input type="number" v-model="activity" />
		<select v-model="aFormat">
			<option>Бк</option>
			<option>кБк</option>
			<option>МБк</option>
			<option>ГБк</option>
		</select>

		<button
			:class="['calculate-btn', { disabled: !allOk || !countOk }]"
			@click="calculateClickHandler"
		>
			Рассчитать
		</button>
	</div>
</template>

<script>
export default {
	name: 'Setup',
	props: {
		percentOk: Boolean,
		rnType: String,
		massOk: Boolean,
		countOk: Boolean
	},
	data() {
		return {
			agrState: 'Твердое',
			activity: null,
			aFormat: 'Бк',
			shakeType: false
		}
	},
	computed: {
		allOk() {
			return this.percentOk && this.setupOk && this.massOk
		},
		aBk() {
			if (!this.activity) return 0
			let mult
			switch (this.aFormat) {
				case 'Бк':
					return this.activity
				case 'кБк':
					mult = 1000
					break
				case 'МБк':
					mult = 1000000
					break
				case 'ГБк':
					mult = 1000000000
					break
			}
			return this.activity * mult
		},
		setupOk() {
			return this.activity > 0
		}
	},
	methods: {
		agrStateButtonClickHandler(e) {
			if (this.agrState == e.target.value) return
			this.agrState = e.target.value
		},
		rnTypeStateButtonClickHandler(e) {
			if (this.rnType == e.target.value) return
			this.$emit('rnTypeChange', e.target.value)
		},
		calculateClickHandler() {
			if (this.allOk && this.countOk) {
				this.$emit('calculate', this.aBk)
			} else if (this.allOk && !this.countOk) {
				this.shakeTypeSelector()
			}
		},
		shakeTypeSelector() {
			this.shakeType = true
			setTimeout(() => {
				this.shakeType = false
			}, 1000)
		}
	}
}
</script>

<style scoped lang="scss">
.setup {
	padding: 20px;
	display: grid;
	grid-template-columns: repeat(3, 1fr);
	grid-gap: 10px;
}
.setup-col-2 {
	grid-column: 1 / 4;
	display: grid;
	grid-template-columns: 1fr 1fr;
}
.center {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}
.agr-state-buttons-container {
	margin: 10px;
	display: flex;
	flex-wrap: nowrap;
	.agr-state-button {
		padding: 10px;
		margin: 0;
		border-radius: 0;
		border-width: 1px 0 1px 1px;
		border-style: solid;
		&:first-child {
			border-radius: 5px 0 0 5px;
		}
		&:last-child {
			border-radius: 0 5px 5px 0;
			border-width: 1px;
		}
		&.pressed {
			background: rgb(80, 155, 209);
			box-shadow: inset 0 0 5px rgb(29, 54, 120);
		}
		&:not(.pressed):hover {
			background: lightgrey;
		}
	}
}
.calculate-btn {
	grid-column: 3;
	padding: 10px;
	color: white;
	font-size: 18px;
	border: none;
	border-radius: 5px;
	background: linear-gradient(
		145deg,
		rgb(29, 54, 120) 0%,
		rgb(80, 155, 209) 100%
	);
	opacity: 0.5;
	&:not(.disabled) {
		opacity: 1;
		&:hover {
			background: linear-gradient(
				145deg,
				rgb(39, 44, 130) 0%,
				rgb(90, 165, 219) 100%
			);
		}
	}
}
.shake {
	animation: shake 0.5s cubic-bezier(0.36, 0.07, 0.19, 0.97) both;
	transform: translateX(0);
	perspective: 1000px;
}

@keyframes shake {
	10%,
	90% {
		transform: translateX(-1px);
	}
	20%,
	80% {
		transform: translateX(2px);
	}
	30%,
	50%,
	70% {
		transform: translateX(-4px);
	}
	40%,
	60% {
		transform: translateX(4px);
	}
}
</style>
