<template>
   <div class="list calc-section">
      <div class="search-input-container">
         <input type="text" v-model="searchText" />
      </div>
      <div class="search-results">
         <div
            class="list-item"
            v-for="item in searchResults"
            :key="'list_item_' + item.Name_RN + item.ID"
            @click="listItemClickHandler(item)"
         >
            <span>{{ item.Name_RN }}</span>
            <span>{{ item.Name_RN_Lat }}</span>
         </div>
      </div>
   </div>
</template>

<script>
export default {
   name: 'List',
   props: {
      list: Array
   },
   data() {
      return {
         searchText: ''
      }
   },
   computed: {
      searchResults() {
         const regEx = new RegExp(this.searchText, 'i')
         return this.list.filter(
            item => regEx.test(item.Name_RN) || regEx.test(item.Kod_RN)
         )
      }
   },
   methods: {
      listItemClickHandler(item) {
         this.$emit('add-item', item)
      }
   }
}
</script>

<style scoped lang="scss">
.list {
   padding: 10px;
   display: flex;
   flex-direction: column;
}
.search-input-container {
   height: 50px;
   input {
      margin: 10px 0;
   }
}
.search-results {
   overflow: auto;
}
.list-item {
   cursor: pointer;
   user-select: none;
   &:hover {
      background: rgba(1, 1, 1, 0.1);
   }
   span:last-child {
      float: right;
   }
}
</style>
